.indicator {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 3rem;
    height: 3rem;
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    padding-left: 1px;
    padding-bottom: 4px;
    border: 5px solid #303030;
    border-radius: 100%;
    background-color: #303030;
    font-size: 1.75rem;
  }
  
  .indicator.correct {
    border-color: #30d830;
    color: #30d830;
  }
  
  .indicator.incorrect {
    border-color: #d03030;
    color: #d03030;
  }
  
  .indicator.hidden {
    opacity: 0;
    transition: opacity 0.25s ease-in-out;
  }