button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 8rem;
    height: 2.75rem;
    border: 1px solid #fff;
    border-radius: 8px;
    padding: 0.5rem 0.5rem;
    background-color: #242424;
    color: #fff;
    font-size: 1.25rem;
    font-family: 'Quicksand', sans-serif;
    transition: all 50ms ease-in-out;
}

button:hover {
    border-color: #50c0f0;
    color: #50c0f0;
    transform: scale(1.025);
}

button:active {
    transform: scale(1);
    transition: none;
}