* {
  box-sizing: border-box;
}

body {
  background-color: #181818;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  text-align: center;
  font-family: 'Quicksand', sans-serif;
}

.game-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
  border-radius: 8px;
  padding: 1rem;
  background-color: #303030;
}

.button-container {
  display: flex;
  justify-content: space-between;
  width: 26rem;
}